/** ====================================================================================================================
// Style Mincer
// ================================================================================================================== */
/** ====================================================================================================================
// Variables - Global
// ================================================================================================================== */
/** ====================================================================================================================
// Layout
// ================================================================================================================== */
/** ====================================================================================================================
// Variables - Global
// ================================================================================================================== */
html {
  cursor: initial; }

body {
  overflow-x: hidden;
  margin: 0;
  font-family: "Montserrat", sans-serif, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.page {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column; }

.header {
  height: 5em; }

.sidebar {
  width: 5em; }

.main {
  position: relative;
  z-index: 1;
  flex-grow: 1;
  display: flex;
  flex-direction: row; }

.section {
  height: 100vh; }

/** ====================================================================================================================
// Fonts
// ================================================================================================================== */
/** ====================================================================================================================
// Typography
// ================================================================================================================== */
/** ====================================================================================================================
// Variables - Global
// ================================================================================================================== */
body {
  font-family: "Montserrat", sans-serif, sans-serif;
  font-size: 16px;
  color: #333; }

h1, h2, h3, h4, h5 {
  font-family: "Heebo", sans-serif, sans-serif;
  margin: 0;
  clear: both; }

h1 {
  font-size: 3em;
  margin-bottom: 0.33333em; }

h2 {
  font-size: 2.25em;
  margin-bottom: 0.44444em; }

h3 {
  font-size: 1.5em;
  margin-bottom: 0.66667em; }

h4, h5 {
  font-size: 1.25em;
  margin-bottom: 0.8em; }

p {
  margin: 0 0 0.5em 0;
  line-height: 1.5em; }
  p br {
    content: '';
    display: block;
    margin-bottom: 0.5em;
    clear: both; }
  p.intro {
    font-size: 1.25em;
    margin: 0 0 0.4em 0; }
    p.intro br {
      margin-bottom: 0.4em; }

button {
  font-size: 1em; }
