.hamburger {
  display: block;
  height: 3.75em;
  width: 3.75em;
  margin: 0.625em;
  padding: 0;
  border: 0;
  cursor: pointer;
  background: transparent;
  z-index: 3; }
  .hamburger:focus {
    outline: 0; }
  .hamburger__label {
    display: none; }
  .hamburger__bar {
    width: 1.875em;
    height: 0.25em;
    background-color: #111;
    border-radius: 0.125em;
    display: block;
    margin: 0.25em auto;
    transition: all 0.25s ease-in-out; }
  .hamburger--init .hamburger__bar {
    animation: none !important; }
  .hamburger--close .hamburger__bar:nth-child(1) {
    animation: close-top-o 0.25s;
    animation-fill-mode: forwards;
    animation-iteration-count: 1; }
  .hamburger--close .hamburger__bar:nth-child(2) {
    opacity: 1; }
  .hamburger--close .hamburger__bar:nth-child(3) {
    animation: close-bottom-o 0.25s;
    animation-fill-mode: forwards; }
  .hamburger--close.hamburger--expanded .hamburger__bar {
    visibility: visible; }
    .hamburger--close.hamburger--expanded .hamburger__bar:nth-child(1) {
      animation: close-top-x 0.25s;
      animation-fill-mode: forwards; }
    .hamburger--close.hamburger--expanded .hamburger__bar:nth-child(2) {
      opacity: 0; }
    .hamburger--close.hamburger--expanded .hamburger__bar:nth-child(3) {
      animation: close-bottom-x 0.25s;
      animation-fill-mode: forwards; }
  .hamburger--arrow.hamburger--expanded .hamburger__bar:nth-child(1), .hamburger--expanded.hamburger--arrow-up .hamburger__bar:nth-child(1), .hamburger--expanded.hamburger--arrow-left .hamburger__bar:nth-child(1) {
    transform: translateY(0.25em) translateX(0.25em) rotate(45deg) scaleX(0.5); }
  .hamburger--arrow.hamburger--expanded .hamburger__bar:nth-child(2), .hamburger--expanded.hamburger--arrow-up .hamburger__bar:nth-child(2), .hamburger--expanded.hamburger--arrow-left .hamburger__bar:nth-child(2) {
    transform: translateY(0.0625em) rotate(90deg) scaleX(0.6); }
  .hamburger--arrow.hamburger--expanded .hamburger__bar:nth-child(3), .hamburger--expanded.hamburger--arrow-up .hamburger__bar:nth-child(3), .hamburger--expanded.hamburger--arrow-left .hamburger__bar:nth-child(3) {
    transform: translateX(-0.25em) translateY(-0.75em) rotate(-45deg) scaleX(0.5); }
  .hamburger--arrow-up.hamburger--expanded {
    transform: rotate(0); }
  .hamburger--arrow-down.hamburger--expanded .hamburger__bar:nth-child(1), .hamburger--expanded.hamburger--arrow-right .hamburger__bar:nth-child(1) {
    transform: translateY(0.8125em) translateX(0.25em) rotate(-45deg) scaleX(0.5); }
  .hamburger--arrow-down.hamburger--expanded .hamburger__bar:nth-child(2), .hamburger--expanded.hamburger--arrow-right .hamburger__bar:nth-child(2) {
    transform: translateY(0.0625em) rotate(90deg) scaleX(0.6); }
  .hamburger--arrow-down.hamburger--expanded .hamburger__bar:nth-child(3), .hamburger--expanded.hamburger--arrow-right .hamburger__bar:nth-child(3) {
    transform: translateX(-0.25em) translateY(-0.1875em) rotate(45deg) scaleX(0.5); }
  .hamburger--arrow-left.hamburger--expanded {
    transform: rotate(-90deg); }
  .hamburger--arrow-right.hamburger--expanded {
    transform: rotate(-90deg); }

@keyframes close-top-o {
  0% {
    transform: translateY(0.5em) rotate(45deg); }
  50% {
    transform: translateY(0.5em) rotate(0); }
  100% {
    transform: translateY(0); } }

@keyframes close-top-x {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(0.5em); }
  100% {
    transform: translateY(0.5em) rotate(45deg); } }

@keyframes close-bottom-o {
  0% {
    transform: translateY(-0.5em) rotate(-45deg); }
  50% {
    transform: translateY(-0.5em) rotate(0); }
  100% {
    transform: translateY(0); } }

@keyframes close-bottom-x {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(-0.5em); }
  100% {
    transform: translateY(-0.5em) rotate(-45deg); } }
